.divider-text {
    display: flex;
    column-gap: 8px;
    align-items: center;
    font-size: 13px;
    line-height: 18px;
    color: $color-noble;
    margin-top: 16px;
    margin-bottom: 16px;
    width: 100%;

    &__line {
        height: 1px;
        margin-top: 2px;
        flex-grow: 1;
        background-color: $border-default;
    }
}