.button-social {
    display: flex;
    column-gap: 10px;
    align-items: center;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    color: $color-night-rider;
    text-decoration: none;
    padding-left: 16px;
    padding-right: 16px;
    font-family: $font-family--manrope;
    justify-content: center;
    border: 1px solid $border-default;
    border-radius: 4px;
    height: 38px;
    transition: background-color 300ms, border-color 300ms;


    &:hover, &:focus {
        background-color: $background-secondary-hover;
        border-color: $background-secondary-active;
        outline: none;
    }

    &:active {
        background-color: $background-secondary-active;
        border-color: $background-secondary-active;
    }


    &__icon {
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            width: 100%;
            height: 100%;
        }
    }


}

    
